import {NavLink} from "react-router-dom";
import dialogIStyle from "./DialogItem.module.css";

function DialogItem(props) {
    return (
        <div className={dialogIStyle.dialogItem}>
            <li>
                <NavLink
                    to={`/dialogs/${props.id}`}
                    activeClassName={dialogIStyle.active}
                    onClick={props.onClick}
                >
                    {props.user.name}
                </NavLink>
            </li>
        </div>
    );
}

export default DialogItem;
