class Dialogs extends React.Component{
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.state = {
            sender: null,
            users: [
                {
                    name: "Andrew",
                    selected: false
                },
                {
                    name: "Dmitry",
                    selected: false
                },
                {
                    name: "Sasha",
                    selected: false
                },
                {
                    name: "Sveta",
                    selected: false
                },
                {
                    name: "Valera",
                    selected: false
                },
                {
                    name: "Viktor",
                    selected: false
                }
            ]
        };
    }

    handleClick(index) {
        let usersUpdate = this.state.users.slice();
        usersUpdate.map(user => user.selected = false);
        usersUpdate[index].selected = true;
        console.log('Users', this.state.users);

        this.setState({sender: usersUpdate[index], users: usersUpdate});
    }

    renderDialogItem(user, index) {
        return (
            <div>
                <DialogItem id={index} user={user} onClick={() => this.handleClick(index)}/>
            </div>
        );
    }

    render() {
        let sender = this.state.sender?.name;

        return (
            <div className={dialogStyle.dialogContainer}>
                <h1 className={dialogStyle.title}>DIALOGS</h1>
                <div className={dialogStyle.dialogsList}>
                    <ul>
                        {this.state.users.map((user, index) => this.renderDialogItem(user, index))}
                    </ul>
                </div>
                <div className={dialogStyle.messages}>
                    <Message sender={sender} messText={"Text of message1"}/>
                    <Message sender={"Me"} messText={"Text of message2"}/>
                </div>

            </div>
        );
    }
}

export default Dialogs;
