import Calculator from "./Calculator";

function Project1() {
    return(
        <div>
            <h1>Project 1 - Подъем состояния</h1>
            <div>
                <p>Калькулятор температуры, вычисляющий вскипит ли вода при заданной температуре.</p>
                <p>Есть два поля ввода: для ввода градусов Цельсия и поле ввода по шкале Фаренгейта. Оба поля синхронизируются.</p>
            </div>
            <Calculator/>
        </div>
    );
}

