import newNewsStyle from './NewNews.module.css'
import news_img from '../../../images/news-1.jpg'
import like from '../../../images/like.png'
import item from '../../../images/news_item.png'

const newsSender = "Вести.Севастополь";
const newsTitle = "Новый терминал аэропорта Симферополь обслужил 15 миллионов пассажиров";
const newsContent = "Первый рейс аэровокзал принял 16 апреля 2018 года, а 15 августа 2020 года обслужил рекордное количество пассажиров за все время существования аэропорта – 44 850.";
function NewNews() {
    return (
        <div className={newNewsStyle.newsItem}>
            <div className={newNewsStyle.newsSender}>
                <img src={item}/>
                <span>{newsSender}</span>
            </div>
            <div className={newNewsStyle.newsTitle}>{newsTitle}</div>
            <div className={newNewsStyle.newsContent}>{newsContent}</div>
            <div className={newNewsStyle.newsImg}>
                <img src={news_img}/>
            </div>
            <div className={newNewsStyle.newsLikes}><span>23</span><img src={like} title='likes'></img></div>
        </div>
    );
}

export default NewNews;
